.side_box {
    position: fixed;
    top: 40px;
    right: calc(50% - 540px);
}

.side_box .side_box_item {
    opacity: 0.3;
    padding: 0px 2px;
    transition: 0.2s all;
    cursor: pointer;
    user-select: none;
}

.side_box .side_box_item.active {
    opacity: 1;
    padding: 14px 2px;
}

.side_box .side_box_item:hover {
    opacity: 1;
}

.side_box .side_box_item_title {
    color: #353535;
    font-size: 15px;
    padding: 2px 4px;
    box-sizing: border-box;
    transition: 0.2s all;
    border-bottom: 2px solid rgba(20, 20, 20, 0);
}

.side_box .side_box_item.active .side_box_item_title {
    padding: 4px 4px;
    border-bottom: 2px solid rgba(20, 20, 20, 0.16);
}