.live_version_info {
    margin-top: -28px;
    font-size: 15px;
    text-shadow: 0px 2px 5px rgb(0 0 0 / 15%);
    background-color: #eaecf7;
    border: 2px dashed #6179ff;
    padding: 12px 24px;
    line-height: 1.6;
    color: #424346;
    display: none;
}

.live_version_info span {
    color: #6179ff;
}

.live_version_info a {
    color: #6179ff;
    border-bottom: 1px dashed #6179ff;
}

.live_version_info i {
    padding: 12px;
    float: right;
    color: #6179ff;
    font-size: 42px;
}