.parts {
    background: #fff;
    border-radius: 0px 0px 5px 5px;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.15);
    -webkit-box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.15);
    -moz-box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.15);
}

.top_part {
    margin: 40px 0px;
    border-top: 3px solid #6471FF;
}

.bottom_part {
    margin: 60px 0px;
    border-top: 3px solid #6471FF;
}

.bottom_part .part_title_container {
    text-align: center;
    margin: -20px auto 0px;
    position: relative;
    padding-top: 5px;
    z-index: 10;
}

.bottom_part .part_title {
    font-size: 20px;
    color: #6471FF;
    border-radius: 100px;
    background-color: #FFF;
    border: 3px solid #6471FF;
    margin: 0px auto;
    padding: 5px 12px;
}

.part_title_extra_text {
    color: rgb(100, 113, 255);
    font-size: 13px;
    font-weight: 700;
}

.right_absolute_extra {
    position: absolute;
    top: -10px;
    right: 5px;
}

.left_absolute_extra {
    position: absolute;
    top: -10px;
    left: 5px;
}

.rounded_fill {
    background-color: #6471FF;
    padding: 2px 5px;
    border-radius: 100px;
    color: #fff;
}

.part_context {
    position: relative;
}

.bottom_part .part_context .sec_middle {
    width: 4px;
    background-color: #EDEDED;
    position: absolute;
    height: 100%;
    left: 50%;
    margin-left: -2px;
    border-radius: 100px;
    top: 0px;
}

.bottom_part .part_context .sec_context {}

.bottom_part .part_context {}

.work_container {
    padding: 0px 28px 0px 50px;
    font-family: "Nunito Sans", Tahoma;
    position: relative;
    margin-top: 34px;
}

.work_container .line {
    width: 4px;
    background-color: #EDEDED;
    position: absolute;
    border-radius: 100px;
    top: 0px;
    bottom: 0px;
    left: 26px;
}

.work_container i {
    font-size: 20px;
    border-radius: 100%;
    background-color: #FF5B5B;
    color: #fff;
    margin-left: -13px;
    padding: 6px;
    margin-top: -5px;
}

.work_container .job_title {
    color: #FF5B5B;
    margin-bottom: 18px;
    display: flex;
    align-items: center;
}

.work_container .job_title span {
    font-size: 16px;
    font-weight: 700;
    padding-left: 8px;
}

.work_container .company_name {
	font-size: 14px;
	color: #737373;
	margin-bottom: 8px;
}

.work_container .time_range {
    font-size: 13px;
    font-family: "Roboto Mono", monospace;
    color: #737373;
    font-weight: 700;
    margin-bottom: 16px;
    margin-left: -4px;
}

.work_container .time_range .time {
    background-color: #6471FF;
    border-radius: 100px;
    color: #fff;
    padding: 2px 8px;
}

.work_container .job_description {
    font-size: 14px;
    font-family: "Nunito Sans", Tahoma;
    padding-bottom: 16px;
    line-height: 1.9;
    color: #222;
}

.project_container {
    position: relative;
    font-family: "Nunito Sans", Tahoma;
    margin: 20px 0px;
    padding: 10px 0px;
}

.projects .sec_context .project_container:first-child .project_images {
    margin-top: 0px !important;
}

.projects .project_images {
    margin-top: 60px;
    background-color: #000;
    display: flex;
    flex-direction: column;
    border-bottom: 6px solid #a8594f;
}

.project_images .image_view {
    position: relative;
    height: 225px;
	background-size: contain;
	background-position: 50%;
    background-repeat: no-repeat;
    display: flex;
    align-items: center;
    justify-content: center;
}

.project_images .image_view video {
    height: 100%;
}

.project_images .image_view .full_screen {
    position: absolute;
    right: 30px;
    top: 34px;
    width: 26px;
    height: 26px;
    padding: 8px;
    background-image: url('../../assets/images/fullscreen.png');
    background-size: 100%;
    box-sizing: border-box;
    cursor: pointer;
}

.project_images .image_view .scale_down {
    position: absolute;
    right: 30px;
    top: 34px;
    width: 26px;
    height: 26px;
    padding: 8px;
    background-image: url('../../assets/images/close.png');
    background-size: 100%;
    box-sizing: border-box;
    cursor: pointer;
}

.project_images .image_view .open {
    position: absolute;
    right: 30px;
    top: 70px;
    width: 26px;
    height: 26px;
    padding: 8px;
    background-image: url('../../assets/images/open.png');
    background-size: 100%;
    box-sizing: border-box;
    cursor: pointer;
}

.project_images .image_view .open:hover {
    background-color: rgba(255, 255, 255, 0.3);
}

.project_images .image_view .full_screen:hover {
    background-color: rgba(255, 255, 255, 0.3);
}

.project_images .image_view .scale_down:hover {
    background-color: rgba(255, 255, 255, 0.3);
}

.project_images .image_list {
	display: flex;
    width: auto;
    margin: auto 0;
    align-self: center;
}

.project_images .image_list img {
    width: 50px;
    height: 50px;
    border: 2px solid #2f2f2f;
    padding: 2px;
    margin-left: 10px;
    margin-top: 8px;
    margin-bottom: 4px;
    cursor: pointer;
    user-select: none;
    object-fit: cover;
}

.project_images .image_list img.selected {
	border: 2px solid #a8594f;
}

.project_sides {
    width: 50%;
    box-sizing: border-box;
    padding: 10px;
}

.project_side_one {}

.project_side_two {
    position: relative;
}

.float_left {
    float: left;
}

.float_right {
    float: right;
}

.project_title {
    font-size: 16px;
    color: #6471FF;
    font-weight: 700;
    border-radius: 100px;
    padding: 5px 13px;
    padding-top: 3px;
    margin: 0px 5px;
    line-height: 1.5;
}

.project_context {
    padding: 10px;
    font-size: 12px;
    line-height: 1.7;
    border-radius: 10px;
    margin-top: 10px;
}

.project_dec {
    font-size: 13px;
}

.project_dec a {
    color: rgb(255, 255, 255);
}

.project_parts {
    font-size: 13px;
    color: #212232;
    margin-left: 10px;
}

.project_parts ul {
    margin: 0px;
    padding: 0;
}

.project_parts ul li span {
    color: rgb(43, 45, 72);
}

.project_parts ul li {
    color: rgb(131, 135, 171);
