}

.project_info {}

.project_side_two.float_right {
    text-align: left;
}

.project_side_two.float_left {
    text-align: right;
}

.project_side_one.float_left {
    text-align: right;
}

.project_side_one.float_right {
    text-align: left;
}

.float_right .project_title {
    float: left;
}

.float_left .project_title {
    float: right;
}

.float_right .project_parts {
    direction: ltr;
    margin-left: 10px;
}

.float_left .project_parts {
    margin-right: 10px;
}

.project_ball {
    width: 18px;
    height: 18px;
    border-radius: 100px;
    position: absolute;
    right: -10px;
    z-index: 10;
    top: 14px;
    margin-left: -10px;
    border: 1px solid #b9bfff;
}

.project_timeline_container {
    position: sticky;
    background-color: #6471ff;
    height: 30px;
    width: 100%;
    top: 0;
    z-index: 10;
}

.project_timeline_steps_container{
    display: flex;
}

.project_timeline_step{
    flex: 1;
    height: 20px;
    background: #fff;
}

.project_timeline_step:hover{
    height: 30px;
}

.project_ball_inner {
    background: #B9BFFF none repeat scroll 0% 0%;
    width: 10px;
    height: 10px;
    border-radius: 100px;
    position: absolute;
    left: 50%;
    z-index: 10;
    top: 4px;
    margin-left: -5px;
}

.project_info_text {
    font-size: 13px;
    font-family: "Roboto Mono", monospace;
    color: #6471FF;
    font-weight: 700;
    margin: 3px 10px 8px;
}

.project_type {
    background-color: #FF7373;
    border-radius: 100px;
    color: #fff;
    padding: 2px 8px;
}

.project_info_files {
    margin-top: 24px;
}

.project_info_files i {
    font-size: 16px;
    padding-right: 3px;
}

.project_for_who {
    background-color: #56D789;
    border-radius: 100px;
    color: #fff;
    padding: 2px 8px;
}

.project_file_image {
    background-color: #C255F2;
    border-radius: 100px;
    color: #fff;
    padding: 2px 8px;
}

.project_status {
    background-color: #ECC53E;
    border-radius: 100px;
    color: #fff;
    padding: 2px 8px;
}

.project_time {
    font-size: 16px;
    margin-bottom: 12px;
}

